import React, { useState } from 'react';
import { connect } from 'react-redux';
import authActions from '../redux/authActions';
import { Redirect } from 'react-router-dom';
import Swal from 'sweetalert2'

const Login = (props) => {
    const [user, setUser] = useState({username: '', password: ''})
    const [loading, setLoading] = useState(false)

    const fieldHandler = e => {
        const field = e.target.name
        const value = e.target.value
        setUser({...user, [field]: value})
    }

    const submitHandler = async e => {
        e.preventDefault()
        if (user.username === '' || user.password === '') {
            return Swal.fire({
                text: 'Debes completar ambos campos!',
                icon: 'error',
                confirmButtonText: 'Reintentar'
              })
        } 
        setLoading(true)
        // Send username/password to be checked if OK
        await props.logUser(user)
        setLoading(false)
    }

    if (props.auth.username) {
        return <Redirect to="/" />
    }

    return ( 
        <div className="loginContainer z-depth-5">
            <form className="loginForm">
                <div className="input-field">
                    <h5>Bienvenido/a...</h5>
                </div>
                <div className="input-field mt2vh">
                    <input type="text" name="username" id="username" value={user.username} autoComplete="off"
                    onChange={fieldHandler}></input>
                    <label htmlFor="username">Nombre de Usuario</label>
                </div>
                <div className="input-field mt2vh">
                    <input type="password" name="password" id="password" value={user.password}
                    onChange={fieldHandler}></input>
                    <label htmlFor="password">Contraseña</label>
                </div>
                <div className="input-field mt2vh">
                    <button className="btn btn-large indigo darken-4" disabled={loading}
                    onClick={submitHandler}>Ingresar</button>
                </div>
            </form>
        </div>
     );
}

const mapStateToProps = state => {
    return {
        auth: state
    }
}

const mapDispatchToProps = {
    logUser: authActions.logUser
}
 
export default connect(mapStateToProps, mapDispatchToProps)(Login);