import axios from 'axios'
import {API} from '../BE'

const tripActions = {
    newTrip: trip => {
        return async (dispatch, getState) => {
            // Send trip to backend and awaits for answer
            const response = await axios.post(`${API}/api/trips/${trip.emisor}`, {...trip})
            return response.data
        }
    },

    getOtherUsers: () => {
        return async (dispatch, getState) => {
            // Ask for the list of users 
            const response = await axios.get(`${API}/api/users`)
            return response.data
        }
    },

    saveFriends: (friends, username) => {
        return async (dispatch, getState) => {
            // Save new friends
            const response = await axios.put(`${API}/api/users/friends`, {username, friends})
            return response.data
        }
    },

    getTrips: (username, status) => {
        return async (dispatch, getState) => {
            // Get list of trips with a particular status
            const response = await axios.get(`${API}/api/trips/${username}/${status}`)
            return response.data
        }
    },

    deleteTrip: (tripNumber, username) => {
        return async (dispatch, getState) => {
            // Delete selected trip
            const response = await axios.delete(`${API}/api/trips/${username}/trip/${tripNumber}`)
            return response.data
        }
    },

    modifyTrip: (username, trip) => {
        return async (dispatch, getState) => {
            // Modifies selected trip
            const response = await axios.put(`${API}/api/trips/${username}`, {...trip})
            return response.data
        }
    },

    getHistoryTrips: (username, status) => {
        return async (dispatch, getState) => {
            // Get trips list according to a specific status and agency who requests it
            const response = await axios.get(`${API}/api/trips/${username}/${status}`)
            return response.data
        }
    },

    takeTrip: (username, numero) => {
        return async (dispatch, getState) => {
            // Take a trip from pendings
            const response = await axios.get(`${API}/api/trips/${username}/trip/${numero}`)
            return response.data
        }
    },

    returnTrip: (username, numero) => {
        return async (dispatch, getState) => {
            // Return a trip from taken to pendings
            const response = await axios.put(`${API}/api/trips/${username}/trip/${numero}`)
            return response.data
        }
    }
}

export default tripActions