import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import tripActions from '../redux/tripActions';
import SingleTrip from '../partials/SingleTrip';
import Swal from 'sweetalert2'
import TripForm from '../partials/TripForm';

const Pendientes = (props) => {
    const [trips, setTrips] = useState([])
    const [loading, setLoading] = useState(false)
    const [modifyTrip, setModifyTrip] = useState(false)

    useEffect(() => {
        if (props.socket) {
            props.socket.on('reloadOrder', () => {
                return getTrips()})
        }
        getTrips()
    }, [props.auth.username, props.socket])

    const getTrips = async () => {
        const response = await props.getTrips(props.auth.username, 'pending')
        // Change date format to DD-MM-YYYY and order trips
        var orderedTrips = []
        if (response.success) {
            const changedDate = response.trips.map(trip => {
                let newTrip = {...trip, 
                    fecha: `${trip.fecha.split('-')[2]}-${trip.fecha.split('-')[1]}-${trip.fecha.split('-')[0]}`,
                    dateForOrder: new Date(trip.fecha.split('-')[0], trip.fecha.split('-')[1]-1, trip.fecha.split('-')[2])}
                return newTrip
            })
            orderedTrips = changedDate.sort(function (a, b) {
                return (a.dateForOrder - b.dateForOrder)
            })
        }
        setTrips(orderedTrips)
        const title = orderedTrips.length > 0 ? `( ${orderedTrips.length } ) Sistema Nube | CAAR` : 'Sistema Nube | CAAR'
        document.title = title
    }

    const deleteHandler = async e => {
        e.preventDefault()
        const tripNumber = parseInt(e.target.dataset.trip)
        const result = await Swal.fire({
            text: "¿Estas seguro de querer eliminar este viaje?",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: 'green',
            cancelButtonColor: 'red',
            confirmButtonText: 'Si, eliminar',
            cancelButtonText: 'No, cancelar'
          })
        if (result.value) {
            setLoading(true)
            // Sends info to server in order to delete trip number
            const response = await props.deleteTrip(tripNumber, props.auth.username)
            await Swal.fire({
                text: response.success ? 'El viaje ha sido eliminado con éxito.' : 'Ocurrió un error.  Reinténtelo!',
                icon: response.success ? 'success' : 'error',
                confirmButtonText: response.success ? 'OK' : 'Reintentar'
            })
            setLoading(false)
            if (response.success) {
                props.socket.emit('reloadPlease')
                props.history.push('/')
            }
        }
    }

    const modifyHandler = e => {
        e.preventDefault()
        const tripNumber = e.target.dataset.trip
        const tripInfo = trips.filter(trip => trip.numero === parseInt(tripNumber))
        setModifyTrip({...tripInfo[0], fecha: `${tripInfo[0].fecha.split('-')[2]}-${tripInfo[0].fecha.split('-')[1]}-${tripInfo[0].fecha.split('-')[0]}`})
    }

    const fieldHandler = e => {
        var field = e.target.name
        var value = e.target.type === 'checkbox' 
        ? e.target.checked ? true : false 
        : e.target.value        
        value = field === 'restringido'
            ? value === 'true' ? true : false
            : value
        if (field === 'tipo' && value === '') {
            value = 'S'
        }
        if (field === 'restringido' && value === '') {
            value = false
        }
        setModifyTrip({...modifyTrip, [field]: value})
    }

    const submitHandler = async e => {
        e.preventDefault()
        let mandatoryFields = ['fecha', 'hora', 'nombre', 'lOrigen', 'dOrigen']
        var error = false
        mandatoryFields.map(field => {
            error = (modifyTrip[field] === '' || modifyTrip[field] === undefined) ? true : error
            return false
        })
        if (error) {
            return Swal.fire({
                text: 'Los campos marcados con (*) son obligatorios!',
                icon: 'error',
                confirmButtonText: 'Reintentar'
            })
        }
        // If there's no mandatory fields empty, it sends new trip info to backend to modify original trip
        const response = await props.modifyTrip(props.auth.username, modifyTrip)
        await Swal.fire({
            text: response.success ? 'Viaje modificado con éxito' : 'Ha ocurrido un error.  Reinténtelo!',
            icon: response.success ? 'success' : 'error',
            confirmButtonText: response.success ? 'OK' : 'Reintentar'
        })
        setModifyTrip(false)
        if (response.success) {
            props.socket.emit('reloadPlease')}
        props.history.push('/')
    }

    const cancelHandler = e => {
        e.preventDefault()
        setModifyTrip(false)
    }

    const takeHandler = async e => {
        e.preventDefault()
        const tripNumber = e.target.dataset.trip
        const result = await Swal.fire({
            text: "¿Estas seguro de querer aceptar este viaje?",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: 'green',
            cancelButtonColor: 'red',
            confirmButtonText: 'Si, tomarlo!',
            cancelButtonText: 'No, cancelar'
          })
        if (result.value) {
            setLoading(true)
            const response = await props.takeTrip(props.auth.username, tripNumber)
            await Swal.fire({
                text: response.success ? 'El viaje ha sido tomado con éxito.' : 'Ocurrió un error.  Reinténtelo!',
                icon: response.success ? 'success' : 'error',
                confirmButtonText: response.success ? 'OK' : 'Reintentar'
            })
            setLoading(false)
            if (response.success) {
                props.socket.emit('reloadPlease')
                props.history.push('/')
            }
        }
        return false
    }

    return ( 
        <div className="pendingContainer">
            {modifyTrip 
            ? (
                <TripForm trip={modifyTrip} cancelHandler={cancelHandler} fieldHandler={fieldHandler} 
                submitHandler={submitHandler} />
            )
            :
            trips.map(trip => <SingleTrip trip={trip} user={props.auth.username} key={trip.numero}
                deleteHandler={deleteHandler} modifyHandler={modifyHandler} takeHandler={takeHandler} />)
        }
        </div>
     );
}

const mapStateToProps = state => {
    return {
        auth: state
    }
}

const mapDispatchToProps = {
    getTrips: tripActions.getTrips,
    deleteTrip: tripActions.deleteTrip,
    modifyTrip: tripActions.modifyTrip,
    takeTrip: tripActions.takeTrip
}
 
export default connect(mapStateToProps, mapDispatchToProps)(Pendientes);