import React from 'react';
import { NavLink } from 'react-router-dom';
import Logo from '../assets/logoNube.png'
import { connect } from 'react-redux';

const Header = (props) => {
    return ( 
        <header>
            <div className="logo-brand">
                <NavLink to="/"><img src={Logo} className="imagen" alt="Logo Nube CAAR" /></NavLink>
                <h3>Nube CAAR</h3>
            </div>
            {props.auth.username && (
            <div className="menu">
                <ul>
                    <NavLink to="/pendientes">Pendientes</NavLink>
                    <NavLink to="/nuevo-viaje">Nuevo Viaje</NavLink>
                    <NavLink to="/historico">Histórico</NavLink>
                    <NavLink to="/amigos">Amigos</NavLink>
                    <NavLink to="/logout">Logout, {props.auth.name}</NavLink>
                </ul>
            </div>
        )}
        </header>
     );
}

const mapStateToProps = state => {
    return {
        auth: state
    }
}
 
export default connect(mapStateToProps)(Header);