import React, { useEffect, useState } from 'react';
import './styles/mainStyles.css'
import {BrowserRouter, Switch, Route, Redirect} from 'react-router-dom'
import Header from './components/Header';
import Login from './components/Login';
import { connect } from 'react-redux';
import authActions from './redux/authActions';
import NewTrip from './components/NewTrip';
import Amigos from './components/Amigos';
import Pendientes from './components/Pendientes';
import Historico from './components/Historico';
import io from 'socket.io-client'
import {API} from './BE'

function App(props) {
  const [socket, setSocket] = useState(false)

  useEffect(() => {
    if (props.auth.username) {
      setSocket(io(API))
    }},
  [props.auth.username])

  if (!props.auth.username && localStorage.getItem('_id')) {
    // There's no user loged in Redux but there's an _id in LocalStorage (he might have pushed F5)
    props.logUser({_id: localStorage.getItem('_id')})
  }
  
  const routes = (!props.auth.username && !localStorage.getItem('_id'))
  // Rutas sin usuario logueado
  ? (
    <Switch>
      <Route path="/login" component={Login} />
      <Redirect to="/login" />
    </Switch>
  )
  // Rutas con usuario logueado
  : (
    <Switch>
      <Route path="/pendientes" render={props => <Pendientes {...props} socket={socket} />} />
      <Route path="/nuevo-viaje" render={props => <NewTrip {...props} socket={socket} />} />
      <Route path="/historico" render={props => <Historico {...props} socket={socket} />} />
      <Route path="/amigos" render={props => <Amigos {...props} socket={socket} />} />
      <Route path="/logout" render={() => {
        props.logOutUser()
        return false
      }} />
      <Redirect to="/pendientes" />
    </Switch>
  )

  return (
    <BrowserRouter>
      <Header />
      <div className="mainContainer">
        {routes}
      </div>
    </BrowserRouter>
  );
}

const mapStateToProps = state => {
  return {
    auth: state
  }
}

const mapDispatchToProps = {
  logUser: authActions.logUser,
  logOutUser: authActions.logOutUser
}

export default connect(mapStateToProps, mapDispatchToProps)(App);
