import axios from 'axios'
import {API} from '../BE'
import Swal from 'sweetalert2'

const authActions = {
    logUser: user => {
        return async (dispatch, getState) => {
            var userInfo;
            if (user._id) {
                const forcedLog = await axios.get(`${API}/api/users/${user._id}`)
                if (!forcedLog.data.success) {
                    return alert(forcedLog.data.error)
                }
                userInfo = {name: forcedLog.data.user.name, 
                    username: forcedLog.data.user.username,
                    _id: forcedLog.data.user._id}
            } else {
                const tryToLog = await axios.post(`${API}/api/auth`, {...user})
                if (!tryToLog.data.success) {
                    return Swal.fire({
                        text: 'Usuario y/o Contraseña Incorrectos',
                        icon: 'error',
                        confirmButtonText: 'Reintentar'
                      })
                }
                userInfo = {name: tryToLog.data.loggedUser.name,
                    username: tryToLog.data.loggedUser.username,
                    _id: tryToLog.data.loggedUser._id }
            }
            dispatch({type: 'LOG_USER', payload: userInfo})
        }
    },

    logOutUser: () => {
        return async (dispatch, getState) => {
            dispatch({type: 'LOGOUT_USER'})
        }
    }
}

export default authActions