const initialState = {}

const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case 'LOG_USER':
            localStorage.setItem('_id', action.payload._id)
            return {...state, ...action.payload}
        case 'LOGOUT_USER':
            localStorage.clear()
            return {}
        default:
            return state 
    }
}

export default authReducer