import React, { useState, useEffect } from 'react';
import SingleTrip from '../partials/SingleTrip';
import { connect } from 'react-redux';
import tripActions from '../redux/tripActions';
import Swal from 'sweetalert2'

const Historico = (props) => {
    const [display, setDisplay] = useState('')
    const [trips, setTrips] = useState([])
    const [loading, setLoading] = useState(false)

    useEffect(() => {
        if (display !== '') {
            getTrips()
        }
    }, [display])

    const getTrips = async () => {
        // Get trips list (according to user's selection -deleted/taken-)
        const status = display === 'D' ? 'deleted' : 'taken'
        const response = await props.getHistoryTrips(props.auth.username, status)
        if (!response.success) {
            setDisplay('')
            return false
        }
        const trips = display === 'D'
        ? response.trips
        : display === 'E'
            ? response.trips.filter(trip => trip.emisor === props.auth.username)
            : response.trips.filter(trip => trip.receptor === props.auth.username)
        setTrips(trips)
        return false
    }

    const returnHandler = async e => {
        e.preventDefault()
        const tripNumber = parseInt(e.target.dataset.trip)
        const result = await Swal.fire({
            text: "¿Estas seguro de querer devolver este viaje a la nube?",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: 'green',
            cancelButtonColor: 'red',
            confirmButtonText: 'Si, devolver',
            cancelButtonText: 'No, cancelar'
          })
        if (result.value) {
            setLoading(true)
            // Sends info to server in order to return this trip
            const response = await props.returnTrip(props.auth.username, tripNumber)
            await Swal.fire({
                text: response.success ? 'El viaje ha sido devuelto con éxito.' : 'Ocurrió un error.  Reinténtelo!',
                icon: response.success ? 'success' : 'error',
                confirmButtonText: response.success ? 'OK' : 'Reintentar'
            })
            setLoading(false)
            if (response.success) {
                props.socket.emit('reloadPlease')
                props.history.push('/')
            }
        }
    }

    return ( 
        <div className="historicoContainer">
            <div className="historicoMenu">
                <button className={`btn light-blue ${display === 'E' ? 'darken-4' : 'lighten-2'}`}
                onClick={() => setDisplay('E')}>Viajes EMITIDOS</button>
                <button className={`btn light-blue ${display === 'A' ? 'darken-4' : 'lighten-2'}`}
                onClick={() => setDisplay('A')}>Viajes ACEPTADOS</button>
                <button className={`btn light-blue ${display === 'D' ? 'darken-4' : 'lighten-2'}`}
                onClick={() => setDisplay('D')}>Viajes ELIMINADOS</button>
            </div>
            <div className="historicoViajes">
                {trips.map(trip => <SingleTrip trip={trip} user={props.auth.username} 
                returnHandler={returnHandler} loading={loading} />)}
            </div>
        </div>
     );
}

const mapStateToProps = state => {
    return {
        auth: state
    }
}

const mapDispatchToProps = {
    getHistoryTrips: tripActions.getHistoryTrips,
    returnTrip: tripActions.returnTrip
}
 
export default connect(mapStateToProps, mapDispatchToProps)(Historico);