import React, { useState } from 'react';
import '../styles/SingleTrip.css'

const SingleTrip = (props) => {
    const trip = props.trip

    const [moreInfo, setMoreInfo] = useState(false)

    return ( 
        <div className="singleTripContainer z-depth-5">
            <p className="tripNumero">( {trip.numero} )</p>
            <div className="tripFechaHora">
                <span className="material-icons left">schedule</span> 
                <p><strong>{trip.fecha}</strong> a las <strong>{trip.hora}</strong> hs</p>
            </div>
            <p className="tripOrigen">{trip.lOrigen.toUpperCase()}</p>
            <p className="tripDestino">{trip.lDestino && trip.lDestino.toUpperCase()}</p>
            {trip.tipo === 'E' 
                ? <p className="tripTipoEjecutivo">VIP</p> 
                    : trip.tipo === 'P' ? <p className="tripTipoPartner">PARTNER</p> 
                    : <p className="tripTipoEjecutivo"></p>}
            <p className="tripTarifa2">{trip.tarifa2 ? 'TARIFA 2' : ''}</p>
            <span className="material-icons tripPlusInfo right" onClick={() => setMoreInfo(!moreInfo)}>
                {!moreInfo ? 'add_circle' : 'remove_circle'}
            </span>
            {trip.status === 'taken' && (
                <p className="viajeAceptado">{trip.emisor === props.user ? 'Aceptó:' : 'Emitió:'} 
                <span>{trip.emisor === props.user ? trip.receptor : trip.emisor}</span>
                </p>
            )}
            {moreInfo && (
                <div className="moreInfoContainer">
                    <div className="moreInfoPasajero">
                        <span className="material-icons left">face</span> 
                        <p className="moreInfoNombreTelefono"><strong>{trip.nombre}</strong> - ({trip.telefono})</p>
                    </div>
                    <div className="moreInfoLine">
                        <p>ORIGEN:</p>
                        <span>{trip.dOrigen} ({trip.lOrigen})</span></div>
                    <div className="moreInfoLine">
                        <p>DESTINO:</p> 
                        <span>{trip.dDestino} ({trip.lDestino})</span></div>
                    <div className="moreInfoLine observaciones">
                        <p>OBSERVACIONES:</p> 
                        <span>{trip.observaciones}</span></div>
                    <div className="moreInfoParticularidades">
                        {trip.tipo === 'E' && <span className="badge yellow accent-2 black-text">VEHICULO EJECUTIVO</span>}
                        {trip.tipo === 'P' && <span className="badge yellow accent-2 black-text">VEHICULO PARTNER</span>}
                        {trip.paradas && <span className="badge yellow accent-2 black-text">TIENE PARADAS INTERMEDIAS</span>}
                        {trip.conRegreso && <span className="badge yellow accent-2 black-text">ES CON ESPERA Y REGRESO</span>}
                        {trip.tarifa2 && <span className="badge yellow accent-2 black-text">APTO TARIFA 2</span>}
                    </div>
                    <div className="moreInfoEmisorYBotones">
                        <p className="moreInfoEmisor">Emitido por <span>{trip.emisor}</span></p>
                        <div className="moreInfoBotones">
                        {props.user === trip.emisor
                        ? ( <>
                            {trip.status === 'deleted' 
                            ? (
                                <p className="viajeEliminado">VIAJE ELIMINADO</p>
                            )
                            : trip.status === 'taken' 
                            ? (
                                <button className="btn blue darken-4" data-trip={trip.numero}
                                onClick={props.returnHandler}
                                disabled={props.loading}>Devolver a la Nube</button>
                            )
                            : (
                                <>
                                <button className="btn blue darken-4" data-trip={trip.numero}
                                onClick={props.modifyHandler}
                                disabled={props.loading}>Modificar</button>
                                <button className="btn red darken-4" data-trip={trip.numero}
                                onClick={props.deleteHandler}
                                disabled={props.loading}>Eliminar</button>
                                </>
                            )}
                            </>)
                        :  trip.status === 'taken'
                            ? (
                                <button className="btn blue darken-4" data-trip={trip.numero}
                                onClick={props.returnHandler}
                                disabled={props.loading}>Devolver a la Nube</button>
                            ) 
                            : (
                                <button className="btn green darken-4" data-trip={trip.numero}
                                disabled={props.loading}
                                onClick={props.takeHandler}>ACEPTAR!</button>
                            )
                            }
                        </div>
                    </div>
                </div>
            )}
        </div>
     );
}
 
export default SingleTrip;