import React from "react";
import "../styles/TripForm.css"

const TripForm = (props) => {    
    // Converts current date to format YYYY-MM-DD to use it as minimum in datepicker
    var fechaHoy = new Date().toLocaleDateString()
    let ano = fechaHoy.split('/')[2]
    let mes = (parseInt(fechaHoy.split('/')[1]) < 10) ? `0${fechaHoy.split('/')[1]}` : fechaHoy.split('/')[1]
    let dia = (parseInt(fechaHoy.split('/')[0]) < 10) ? `0${fechaHoy.split('/')[0]}` : fechaHoy.split('/')[0]
    var fechaMin = `${ano}-${mes}-${dia}`

  return (
    <div className="tripForm z-depth-5">
      <form>
          <div className="row">
            <div className="input-field col s6 l3">
                <input type="date" name="fecha" id="fecha"
                onChange={props.fieldHandler}
                min={fechaMin}
                value={props.trip && props.trip.fecha}
                ></input>
                <label htmlFor="fecha">Fecha (*)</label>
            </div>
            <div className="input-field col s6 l3">
                <input type="time" name="hora" id="hora"
                onChange={props.fieldHandler}
                value={props.trip && props.trip.hora}
                ></input>
                <label htmlFor="hora">Hora (*)</label>
            </div>
            <div className="input-field col s12 m6 l3">
                <select className="browser-default" name="tipo" id="tipo"
                onChange={props.fieldHandler}
                value={props.trip && props.trip.tipo}
                >
                    <option value="" disabled selected>Tipo de Vehículo</option>
                    <option value="S">Standard</option>
                    <option value="E">Ejecutivo</option>
                    <option value="P">Partner</option>
                </select>
            </div>
            <div className="input-field col s12 m6 l3">
                <select className="browser-default" name="restringido" id="restringido"
                onChange={props.fieldHandler}
                value={props.trip && props.trip.restringido.toString()}
                >
                    <option value="" disabled selected>¿Quiénes verán este viaje?</option>
                    <option value="false">Todas las agencias</option>
                    <option value="true">Solo mis amigos</option>
                </select>
            </div>
          </div>
            <div className="row">
                <div className="input-field col s12 m6">
                    <input type="text" name="nombre" id="nombre"
                    onChange={props.fieldHandler}
                    value={props.trip && props.trip.nombre}
                    autoComplete="off"
                    autoFocus={props.trip}
                    ></input>
                    <label htmlFor="nombre">Nombre del Pasajero (*)</label>
                </div>
                <div className="input-field col s12 m6">
                    <input type="text" name="telefono" id="telefono"
                    onChange={props.fieldHandler}
                    value={props.trip && props.trip.telefono}
                    autoComplete="off"
                    autoFocus={props.trip}
                    ></input>
                    <label htmlFor="telefono">Teléfono del Pasajero</label>
                </div>
            </div>
            <div className="row">
                <div className="input-field col s12 m6">
                    <input type="text" name="dOrigen" id="dOrigen"
                    onChange={props.fieldHandler}
                    value={props.trip && props.trip.dOrigen}
                    autoComplete="off"
                    autoFocus={props.trip}
                    ></input>
                    <label htmlFor="dOrigen">Dirección de Origen (*)</label>
                </div>
                <div className="input-field col s12 m6">
                    <input type="text" name="lOrigen" id="lOrigen"
                    onChange={props.fieldHandler}
                    value={props.trip && props.trip.lOrigen}
                    autoComplete="off"
                    autoFocus={props.trip}
                    ></input>
                    <label htmlFor="lOrigen">Localidad de Origen (*)</label>
                </div>
            </div>
            <div className="row">
                <div className="input-field col s12 m6">
                    <input type="text" name="dDestino" id="dDestino"
                    onChange={props.fieldHandler}
                    value={props.trip && props.trip.dDestino}
                    autoComplete="off"
                    autoFocus={props.trip}
                    ></input>
                    <label htmlFor="dDestino">Dirección de Destino</label>
                </div>
                <div className="input-field col s12 m6">
                    <input type="text" name="lDestino" id="lDestino"
                    onChange={props.fieldHandler}
                    value={props.trip && props.trip.lDestino}
                    autoComplete="off"
                    autoFocus={props.trip}
                    ></input>
                    <label htmlFor="lDestino">Localidad de Destino</label>
                </div>
            </div>
            <div className="row">
                <div className="col s12 l4">
                    <label>
                        <input type="checkbox" className="filled-in" name="paradas" id="paradas" 
                        onChange={props.fieldHandler}
                        checked={props.trip && props.trip.paradas}
                        />
                        <span>¿Tiene paradas intermedias?</span>
                    </label>
                    <br/>
                    <label>
                        <input type="checkbox" className="filled-in" name="conRegreso" id="conRegreso" 
                        onChange={props.fieldHandler}
                        checked={props.trip && props.trip.conRegreso}
                        />
                        <span>¿Es con espera y regreso?</span>
                    </label>
                    <br/>
                    <label>
                        <input type="checkbox" className="filled-in" name="tarifa2" id="tarifa2" 
                        onChange={props.fieldHandler}
                        checked={props.trip && props.trip.tarifa2}
                        />
                        <span>¿Es apto TARIFA 2?</span>
                    </label>
                </div>
                <div className="input-field col s12 l8">
                    <textarea className="materialize-textarea" name="observaciones" id="observaciones"
                    onChange={props.fieldHandler}
                    value={props.trip && props.trip.observaciones}
                    autoFocus={props.trip}
                    ></textarea>
                    <label htmlFor="observaciones">Ingrese aquí información extra</label>
                </div>
            </div>

            <div className="row botones">
              <div className="col s6">
                    <button className="btn red darken-4" onClick={props.cancelHandler}
                    disabled={props.loading}>Cancelar</button>
              </div>
              <div className="col s6">
                    <button className="btn green darken-4" onClick={props.submitHandler}
                    disabled={props.loading}>Validar</button>
              </div>
            </div>
      </form>
    </div>
  );
};

export default TripForm;
