import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import tripActions from '../redux/tripActions';
import Swal from 'sweetalert2'

const Amigos = (props) => {

    const [users, setUsers] = useState([])
    const [myFriends, setMyFriends] = useState([])
    const [loading, setLoading] = useState(false)

    useEffect(() => {
        getUsers()
    }, [props.auth.username])

    const getUsers = async () => {
        const response = await props.getOtherUsers()
        setUsers(response.success ? response.users : [])
        response.success && response.users.map(user => {
            if (user.username === props.auth.username) {
                setMyFriends(user.friends)
            }
            return false
        })
        return false
    }

    const cancelHandler = e => {
        e.preventDefault()
        props.history.push('/')
    }

    const changeHandler = e => {
        if (e.target.checked) {
            setMyFriends([...myFriends, e.target.name])
        } else {
            const newFriends = myFriends.filter(friend => friend !== e.target.name)
            setMyFriends(newFriends)
        }
    }

    const submitHandler = async e => {
        e.preventDefault()
        setLoading(true)
        // Save new friends list
        const response = await props.saveFriends(myFriends, props.auth.username)
        setLoading(false)
        await Swal.fire({
            text: response.success ? 'Lista de amigos modificada!' : response.error,
            icon: response.success ? 'success' : 'error',
            confirmButtonText: response.success ? 'OK' : 'Reintentar'
        })
        if (response.success) {
            props.history.push('/')
        }
    }

    return ( 
            <div className="amigosContainer z-depth-5">
                <p className="titulo flow-text">DEFINI O MODIFICA TUS AMIGOS</p>
                <p className="flow-text">
                    Podés elegir una o más agencias que serán tus "amigos".  En cada viaje tendrás que definir
                    si querés que ese viaje lo vean todos los usuarios o solamente tus amigos.  ¡Podés modificarlo
                    luego si cambiás de idea!
                </p>
                <div className="nombresAmigos row">
                {users.map(user => {
                    if (user.username !== props.auth.username) {
                        return (
                            <label key={user._id} className="col s12 m6 l4">
                                <input type="checkbox" className="filled-in" name={user.username} id={user.username} 
                                checked={myFriends.includes(user.username)} onChange={changeHandler} />
                                <span>{user.name}</span>
                            </label>
                        )
                    }
                    return false
                })}
                </div>
                <div className="amigosBotones row">
                    <div className="col s12 l6">
                        <button className="btn red darken-4"
                        onClick={cancelHandler} disabled={loading}>Cancelar</button>
                    </div>
                    <div className="col s12 l6">
                        <button className="btn green darken-4"
                        onClick={submitHandler} disabled={loading}>Validar</button>
                    </div>
                </div>
            </div>
         );
        }

const mapStateToProps = state => {
    return {
        auth: state
    }
}

const mapDispatchToProps = {
    getOtherUsers: tripActions.getOtherUsers,
    saveFriends: tripActions.saveFriends
}
 
export default connect(mapStateToProps, mapDispatchToProps)(Amigos);