import React, { useState } from 'react';
import TripForm from '../partials/TripForm';
import Swal from 'sweetalert2'
import { connect } from 'react-redux';
import tripActions from '../redux/tripActions';

const NewTrip = (props) => {
    const [newTrip, setNewTrip] = useState({
        paradas: false, conRegreso: false, tarifa2: false, restringido: false})
    
    const [loading, setLoading] = useState(false)

    const fieldHandler = e => {
        var field = e.target.name
        var value = e.target.type === 'checkbox' 
        ? e.target.checked ? true : false 
        : e.target.value        
        value = field === 'restringido'
            ? value === 'true' ? true : false
            : value
        if (field === 'tipo' && value === '') {
            value = 'S'
        }
        if (field === 'restringido' && value === '') {
            value = false
        }
        setNewTrip({...newTrip, [field]: value})
    }

    const submitHandler = async e => {
        e.preventDefault()
        let mandatoryFields = ['fecha', 'hora', 'nombre', 'lOrigen', 'dOrigen']
        var error = false
        mandatoryFields.map(field => {
            error = (newTrip[field] === '' || newTrip[field] === undefined) ? true : error
            return false
        })
        if (error) {
            return Swal.fire({
                text: 'Los campos marcados con (*) son obligatorios!',
                icon: 'error',
                confirmButtonText: 'Reintentar'
            })
        }
        // If there's no mandatory fields empty, it sends trip to backend
        setLoading(true)
        let trip = {...newTrip, emisor: props.auth.username}
        const response = await props.newTrip(trip)
        setLoading(false)
        await Swal.fire({
            text: response.success ? 'Viaje cargado con éxito!' : response.error,
            icon: response.success ? 'success' : 'error',
            confirmButtonText: response.success ? 'OK' : 'Reintentar!'
        })
        if (response.success) {
            return props.history.push('/')
        }
    }

    const cancelHandler = e => {
        e.preventDefault()
        props.history.push('/')
    }

    return ( 
        <div className="newTripContainer">
            <TripForm fieldHandler={fieldHandler} submitHandler={submitHandler}
            cancelHandler={cancelHandler} loading={loading} />
        </div>
     );
}

const mapStateToProps = state => {
    return {
        auth: state
    }
}

const mapDispatchToProps = {
    newTrip: tripActions.newTrip
}

export default connect(mapStateToProps, mapDispatchToProps)(NewTrip);